



.portfoliotab .portfoliotabMain_bg{
    background: #F9F9F9;
  }

  /* faizan  */
  .portfoliotab-design{
    margin-top: 40px;


  }
  .portfoliotab-design .portfoliotab-design-left {

    border-right: 1px solid #c7c7c5;

  }
