  .portfoliotab .portfoliotab_next_pre_btn_background{
    background: #f7f8fa;
  }
  .portfoliotab .portfoliotab_panel_background{
    background: #f7f8fa;
  }
  
  .portfoliotab .Buttoncls {
    display: inline-block;
    padding: 9px 14px;
    cursor: pointer;
    font-family: inherit;
    font-size: 1rem;
    font-weight: bold;
    color: rgb(115 115 115);
    background: #ffffff;
    border: 1px solid #6B6B6A;
    border-radius: 70%;
    border: 1px solid;
    height: 45px;
  
  }
  .portfoliotab .Buttoncls:focus,
  .portfoliotab .Buttoncls:hover {
    outline: 0;
    /* background: #899499; */
  }
  .Tab.is-active .Button {
      background: #ffffff;
      color: #EE9821;
      border-radius: 25px;
      border: 2px solid;
  }
  .portfoliotab .ButtonclsActive {
      background: #ffffff;
      color: #EE9821;
      border: 2px solid;
      padding: 9px 14px;
  }
  
  .container.portfoliotabContainer {
    max-width: 1921px;
  }
  
  
  .portfoliotab .line{
  width: 0px;
  left: 72px;
  border-left: 1.5px dashed #a0a0a0;
  height: 40px;
  margin-left: 24px;
  }
  .Tab.is-active .line{
  border: 2px dashed #EE9821;
  }
  .portfoliotab .lineActive{
  border-left: 1px dashed #EE9821;
  }
  
  .but{
    background: #ffffff;
    border :1px solid #6B6B6A;
    border-radius: 25px;
    border: 2px solid;
  }
  
  .portfoliotab .text2{
    font-family: Poppins;
    line-height: 20px;
    margin: 3%;
    padding-left: 1%; 
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  color: #6B6B6A;
  }
  
  .portfoliotab .tb_nextButton{
   
      background-color: #EE9821;
      width: 116px;
      height:36px;
      border-radius: 2px;
      color: white;
      font-family: 'Poppins', sans-serif;
      text-align: center;
      font-style: normal;
      font-weight: 500px;
      font-size: 14px;
      line-height: 20px;
      float: right;
      cursor: pointer;
      border-radius: 2px;
  
  }
  
  .portfoliotab .tb_prevButton{
     
    font-family: Poppins;
font-size: 14px;
font-style: normal;
font-weight: 500;
line-height: 20px;
letter-spacing: 0.01em;
text-align: left;


  }
  
  
  .tb_prevButton:focus,
  .tb_prevButton:hover {
    outline: 0;
    background: #899499;
  }
  
  .left_img{
     margin-top: 6%;
